.cart {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.cart-cartItems {
    width: 100%;
    display: flex;
    flex-direction: column;

    justify-content: space-between;
    margin-right: 0px;

    
}

.cartItem {
    width: 50%;
    display: flex;
    flex-direction: row;

    border-radius: 15px;
    margin: 15px;
    
}

.cartItem img {
    width: 10vw;
    display: flex;
    margin-left: 0px;  
    margin-right: 10px;  
}

.cartItem .description {
    width: 10px;
    height: 20px;
    font-size: 2vw;
}



.countHandler {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
    margin-left: 5px;
    height: 30px;
}

.countHandler input {
    width: 1vw;
    text-align: center;
    font-weight: bolder;
}

.checkout button {
    width: 180px;
    height: 50px;
    background-color: rgb(19, 19, 19);
    color: white;
    border: none;
    border-radius: 40px;
    margin: 10px;
    cursor: pointer;
    font-weight: bold;
    font-size: 25px;
}

.description b{
    font-weight: bold;
    margin-right: 10px;
    
}

.checkout p {
    text-align: start;
    margin-top: 0px;
    font-size: 1.8vw;
    font-style: italic;
    font-weight: bold;
}


