@media(max-width: 740px){
    .myproduct-container{
        display: block;
    }
    .myproduct_img {
        width: 330px;
        height: 50vh;
    }

    .myproduct_info {
        margin-top: 20px;
        margin-left: 100px;
    }

    .myproduct_info h1{
        font-size:10vw;
        color:#311616;
        font-weight:bold;
    }

    .myproduct_info h2 {
        margin-top: 10px;
        margin-left: 10px;
        font-size: 30px;
        color: seagreen;
    }

    
    p{
        text-align: start;
        margin-top: 1vw;
        font-size: 4.5vw;
        font-style: italic;
        display: inline-block;
        width: 100%;
}

    
 }