.inspect-container{
    width:100%;
    height:97vh;
    background-image: url('../../assets/background-product.png');
    display:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.myproduct-container{
    width:90vw;
    height:88vh;
    display:flex;
    padding-top:1vw;
    background-color: white;
}
.myproduct_img{
    width:auto;
    height:50vh;
}
.myproduct_info{
    width:45vw;
    height:fit-content;
    margin-left:3vw;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

h1{
    font-size:5vw;
    color:#311616;
    font-weight:bold;
}

.myproduct_info h2{
    margin-top: 10px;
    margin-left: 70px;
    font-size: 35px;
    color: seagreen;
}



p{
        text-align: start;
    margin-top: 1vw;
    font-size: 1.8vw;
    font-style: italic;
    }


.myproduct_info p{
    margin-top: 50px;
    font-size: 30px;
    margin-left: 10px;
}